import {ProfileMenuChangePayload, ProfileMenu} from '@here/hds-navbar';
import React, {ReactNode, useEffect, useImperativeHandle} from 'react';

// eslint-disable-next-line no-new
new ProfileMenu();

interface HDSProfileMenuProps extends Omit<React.HTMLAttributes<HTMLInputElement>, 'onChange'> {
  children?: ReactNode;
  profileName?: string;
  profileEmail?: string;
  avatarImageUrl?: string;
  open?: boolean;
  signup?: boolean;
  noTrigger?: boolean;
  onChange?: (e: ProfileMenuChangePayload) => void;
}
const HDSProfileMenu = React.forwardRef(
  (
    {
      profileName,
      profileEmail,
      avatarImageUrl,
      open,
      noTrigger,
      signup,
      onChange,
      children,
      className,
      ...rest
    }: HDSProfileMenuProps,
    ref
  ) => {
    const innerRef = React.useRef<HTMLElement>(null);
    useImperativeHandle(ref, () => innerRef.current);
    const _handleChange = (e: CustomEvent<ProfileMenuChangePayload>) => {
      if (e.target !== innerRef.current) return;
      onChange && onChange(e.detail);
    };
    useEffect(() => {
      if (onChange && innerRef.current) {
        innerRef.current.addEventListener('change', _handleChange as EventListener);
      }
      return () => innerRef.current?.removeEventListener('change', _handleChange as EventListener);
    }, [onChange, innerRef]);

    return (
      <hds-profile-menu
        {...(profileName && {'profile-name': profileName})}
        {...(profileEmail && {'profile-email': profileEmail})}
        {...(avatarImageUrl && {'avatar-image-url': avatarImageUrl})}
        {...(open && {open})}
        {...(signup && {signup})}
        {...(noTrigger && {'no-trigger': noTrigger})}
        {...(className && {class: className})}
        ref={innerRef}
        {...rest}
      >
        {children}
      </hds-profile-menu>
    );
  }
);

HDSProfileMenu.defaultProps = {
  profileName: '',
  profileEmail: '',
  open: false,
  signup: false,
  noTrigger: false,
  avatarImageUrl: '',
};

export default HDSProfileMenu;
